import React from 'react'
import { Button,Input } from 'antd';
import { DatePicker } from 'antd';
import moment from 'moment';

import './makepaper.css'


const dateFormat = 'YYYY/MM/DD';


export  default class Makepaper extends React.Component {
    state = {
        loading: false,
    }

    enterLoading = () => {
        this.setState({ loading: true });
    }

    closeLoading = () => {
        this.setState({ loading: false});
    }

    render() {
        return(
            <div className="homepage">
                <div style={{ marginBottom: 16 }} className="input">
                    <Input addonBefore="重复率要求不超过" addonAfter="%" defaultValue="5" />
                    <br></br>
                    <br></br>
                    <Input addonBefore="学年度" defaultValue="2015" />
                    <br></br>
                    <br></br>
                    <Input addonBefore="科目"  defaultValue="数据结构" />
                    <br></br>
                    <br></br>
                    <Input addonBefore="学期" defaultValue="1" />
                    <br></br>
                    <br></br>
                    <Input addonBefore="选择题覆盖单元" defaultValue="1,2,3" />
                    <br></br>
                    <br></br>
                    <Input addonBefore="填空题覆盖单元" defaultValue="1,2,3" />
                    <br></br>
                    <br></br>
                    <Input addonBefore="计算题覆盖单元" defaultValue="1,2,3" />
                    <br></br>
                    <br></br>
                    <Input addonBefore="简答题覆盖单元" defaultValue="1,2,3" />
                    <br></br>
                    <br></br>
                    <DatePicker defaultValue={moment('2019/01/01', dateFormat)} format={dateFormat} />
                </div>
                <Button type="primary"  loading={this.state.loading} onClick={this.enterLoading}>
                    生成试卷
                </Button>
                <Button icon="download" onClick={this.closeLoading}>
                    下载试卷
                </Button>
                <Button icon="download" onClick={this.closeLoading}>
                    下载答案
                </Button>
            </div>
        )
    }

}